.wp-block-video {
	// Remove the left and right margin the figure is born with.
	margin-left: 0;
	margin-right: 0;

	video {
		max-width: 100%;
	}

	@supports (position: sticky) {
		[poster] {
			object-fit: cover;
		}
	}

	&.aligncenter {
		text-align: center;
	}

	figcaption {
		text-align: center;
		font-size: 80%;
		margin: 5px 0 10px;
	}
}
